.main-portfolio-container {
    position: relative;
    padding: 20px;
    width: 100%;
    height: 100%;
}

.card {
    background: linear-gradient(-45deg, #0f2027, #203a43, #2c5364);
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: hidden;
}

.header {
    background-color: white;
    width: 100%;
    text-align: center;
    align-self: flex-start;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width:825px) {
    .main-portfolio-container {
        background-color: transparent;
    }

    .card {
        background-color: rgba(24, 173, 24, 0.8);
    }

    .header {
        background-color: rgba(255, 255, 255, 0.85);
    }
}

.header>p {
    margin-left: 20px;
    padding: 5px;
    font-size: 35px;
    font-weight: bold;
}

.header>svg {
    font-size: 35px;
    font-weight: bold;
}

@media (max-width:350px) {
    .header>p {
        font-size: 20px;
    }

    .header>svg {
        font-size: 20px;
    }
}